import styled from 'styled-components';

export const Button = styled.button`
   width: 80px;
   height: 100%;

   border: none;
   border-radius: 5px;
   background-color: #f3f3f3;
   transition: .3s;
   outline: none;

   &:hover {
      transition: .3s;
      border: 2px solid #0c1614;
   }
`